
import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/AboutView'
import Portolio from '../views/PortfolioView'
import CV from '../views/CV'


Vue.use(VueRouter)


const routes = [
{
    path: '/',
    name: 'Home',
    component: Home
},
{
    path: '/Portfolio',
    name: 'Portfolio',
    component: Portolio
},
{
    path: '/cv',
    name: 'CV',
    component: CV
}

]







const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router;